import React from 'react';
import Helmet from 'react-helmet';

import GridBlock from '../components/grid-block';
import GridItem from '../components/grid-item';
import PostFull from '../components/post-full';

class BlogPostTemplate extends React.Component {

  render() {
    const { html, frontmatter } = this.props.data.markdownRemark;
    const { title, date, path, tags, description, cover } = frontmatter;

    const post = {
      html: html.substr(html.indexOf('<hr>') + 4),
      // html,
      title,
      date,
      path,
      tags,
      description,
      cover: cover ? cover.childImageSharp.responsiveSizes.src : null,
    };

    // edge.node.frontmatter.cover
    //   ? edge.node.frontmatter.cover.childImageSharp.responsiveSizes
    //   : null,


    return (
      <div>
        <GridBlock>
          <Helmet>
            <title>{`${post.title} - A Race athlete blog`}</title>
            <meta name="description" content={post.description} />
            <meta property="og:title" content={`${post.title} - A Race athlete blog`} />
            <meta property="og:image" content={(post.cover && `https://www.araceathlete.com${post.cover}`) || 'https://www.araceathlete.com/images/logo-512x512.png'} />
            <meta property="og:url" content={`https://www.araceathlete.com/blog${post.path}`} />
            <meta property="og:description" content={post.description} />
            <meta property="og:type" content="article" />
          </Helmet>
          <GridItem page={post} PostType={PostFull} />
        </GridBlock>
      </div>
    );
  }
}

export default BlogPostTemplate

export const pageQuery = graphql`
query BlogPostBySlug($slug: String!) {
    markdownRemark(
      fields: {
        slug: {
          eq: $slug
        }
      }
    )
    {
    html
    frontmatter {
      title
      date
      path
      parent
      tags
      description
      cover {
        childImageSharp {
          responsiveSizes(maxWidth: 412) {
            src
            sizes
            srcSet
            originalImg
            originalName
            aspectRatio
            base64
          }
        }
      }
    }
  }
}
`;



