import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Drawer from 'material-ui/Drawer';
import Button from 'material-ui/Button';
import SvgIcon from 'material-ui/SvgIcon';
import ArrowLeftThickIcon from 'mdi-react/ArrowLeftThickIcon';

import TagArray from '../tag-array';


const propTypes = {

};

const styles = theme => ({
  root: {
    padding: 16,
    '@global': theme.typography.global,
    display: 'flex',
    flexGrow: 1,
    justifyContent: 'center',
  },
  mainStage: {
    maxWidth: 600,
  },
  navIcon: {
    bottom: theme.spacing.unit * 4,
    right: theme.spacing.unit * 2,
    position: 'fixed',
    zIndex: 800,
  },
  titleMain: {
    color: theme.palette.primary.dark,
  },
});

const handleBack = () => document.referrer ? window.close() : window.history.back();

const PostFull = (props) => {
  const { classes, html, path, title, tags } = props;
  return (
    <div className={classes.root}>
      <div className={classes.mainStage}>
        <Button
          variant="fab"
          color="primary"
          aria-label="back"
          onClick={() => handleBack()}
          className={classes.navIcon}
        >
          <SvgIcon>
            <ArrowLeftThickIcon />
          </SvgIcon>
        </Button>
        <h2>{title}</h2>
        <hr />
        <div dangerouslySetInnerHTML={{ __html: html }} />
        <TagArray tags={tags} navClick />
      </div>

    </div>
  );
};

PostFull.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostFull);



